/* Container */
.container {
  display: flex;
  width: 100%;
}

/* Sidebar Styles */

.sidebar {
  height: calc(100vh - 1rem);
  width: 13.5rem;
  background-color: var(--sidebar-bg-color);
  position: relative;
  border-top-left-radius: 0.6rem;
}

.chat-title {
  padding: 1rem;
  height: 3rem;
  font-size: 1rem;
  border-bottom: 2px solid var(--bg-color);
}

.channel-wrapper {
  padding-top: 1rem;
}

.channel {
  display: flex;
  align-items: center;
  margin: 0 0.5rem;
  padding: 0.4rem 0.5rem;
  font-size: 1rem;
  cursor: pointer;
  gap: 0.5rem;
  color: var(--secondary-text);
  border-radius: 5px;
  text-transform: lowercase;
}

.channel::before {
  content: "#";
  font-size: 1.125rem;
  color: var(--secondary-text);
}

.channel:hover {
  background-color: var(--sidebar-hover);
  color: var(--primary-text);
} 

.channel-active {
  background-color: var(--main-bg-color);
  color: var(--primary-text)
}