@import url(https://fonts.googleapis.com/css2?family=Seymour+One&display=swap);
/* Navbar Styles */

.navbar {
	height: calc(100vh - 1rem);
	width: 4.5rem;
}

.icons {
	display: flex;
	flex-direction: column;
	align-items: center;
	list-style: none;
	color: var(--primary-text);
}

.icons a {
	position: relative;
	display: flex;
	font-size: 1.5rem;
	font-weight: 400;
	width: 3rem;
	height: 3rem;
	align-items: center;
	justify-content: center;
	background-color: var(--main-bg-color);
	border-radius: 50%;
	margin: 0.5rem 0 0 0;
	transition: var(--transition);
}

.icons a:hover,
.active {
	border-radius: 35% !important;
	background-color: var(--primary-accent) !important;
	color: white;
	cursor: pointer;
}

.active::before,
.icons > a:hover::before {
	content: '';
	position: absolute;
	height: 75%;
	background-color: white;
	width: 0.35rem;
	left: -11px;
	border-radius: 0 10px 10px 0;
	transition: var(--transition);
	animation: nav-hover 250ms;
}

.icons > a:hover::before {
	transition: var(--transition);
	height: 35%;
}

.divider {
	border: 1px solid var(--main-bg-color);
	width: 50%;
	margin-top: 0.6rem;
}

@keyframes nav-hover {
	from {
		height: 0;
	}
}

/* Footer styles */

.footer {
  position: absolute;
  display: flex;
  background-color: var(--footer-bg-color);
  padding: 0 0.5rem;
  align-items: center;
  bottom: 0;
  width: 13.5rem;
  height: 50px;
}

.guest-name {
  font-size: 0.75rem;
  font-weight: 600;
  color: var(--primary-text);
}

.tag {
  font-size: 0.75rem;
  color: var(--tag-text);
}

#avatar {
  border-radius: 50%;
  margin: 0 0.5rem 0 0;
}

.footer::after {
  content: '';
  position: absolute;
  top: 27px;
  left: 25px;
  border: solid 3px var(--footer-bg-color);
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: var(--green);
  box-sizing: content-box;
  box-sizing: initial;
}
/* Main window styles */

.hypertext:link, .hypertext:visited, .hypertext:hover, .hypertext:active {
  color: var(--hyperlink);
}

.hypertext:hover, .hypertext:active {
  text-decoration: underline;
}

code {
  background: var(--code-bg-color);
  border: 1px solid var(--code-border-color);
  page-break-inside: avoid;
  display: block;
  padding: 0.5rem;
  margin: 0.2rem 0;
  border-radius: 0.25rem;
  word-wrap: break-word;
}

.main-window {
	height: calc(100vh - 1rem);
	background-color: var(--main-bg-color);
	flex: 1 1;
}

.header-wrapper {
	display: flex;
	height: 3rem;
	align-items: center;
	padding: 0 1rem;
	border-bottom: 2px solid var(--bg-color);
}

.channel-title::before {
	content: '#';
	font-size: 1.125rem;
	color: var(--secondary-text);
	font-weight: 400;
	margin-right: 0.5rem;
}

.message-window {
	position: relative;
	padding-top: 1rem;
	display: flex;
	flex-direction: column;
	overflow-y: scroll;
	scrollbar-width: thin;
	height: calc(100% - 50px - 4rem - 1rem);
	/*  so this is awkward
		100% because i want it to span the entire page
		- 50px because of the guest footer
		- 4rem because of the padding on the chatbox
		- 1rem because of the font size in the chatbox
    */
}

.message-container {
	display: flex;
	flex-direction: column;
}

.message-info {
	display: flex;
	margin: 0 calc(40px + 2rem);
	grid-gap: 0.6rem;
	gap: 0.6rem;
}

.message {
	/* display: flex; */
	position: relative;
	color: var(--message-text);
	margin: 0.5rem calc(40px + 2rem);
    align-items: flex-end;
	/* 40px (size of avatar) + 2rem (twice the amount of space on the left property in the .message-avatar class */
}

.edited::after {
  content: ' (edited)';
  font-size: 0.5rem;
  color: var(--secondary-text);
}

.message-name {
  font-weight: 600;
  color: var(--primary-text);
}

.message-avatar {
	border-radius: 50%;
	width: 40px;
	height: 40px;
	position: absolute;
	left: 1rem;
}

.message-time {
  color: var(--secondary-text);
	font-size: 0.75rem;
	align-self: flex-end;
}
.chatbox {
  margin: 1rem auto;
  height: 50px;    
  background-color: var(--chat-bg-color);
  color: var(--chat-text);
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  padding: 0 2rem;
  width: 95%;
  cursor: no-drop;
}
/* Container */
.container {
  display: flex;
  width: 100%;
}

/* Sidebar Styles */

.sidebar {
  height: calc(100vh - 1rem);
  width: 13.5rem;
  background-color: var(--sidebar-bg-color);
  position: relative;
  border-top-left-radius: 0.6rem;
}

.chat-title {
  padding: 1rem;
  height: 3rem;
  font-size: 1rem;
  border-bottom: 2px solid var(--bg-color);
}

.channel-wrapper {
  padding-top: 1rem;
}

.channel {
  display: flex;
  align-items: center;
  margin: 0 0.5rem;
  padding: 0.4rem 0.5rem;
  font-size: 1rem;
  cursor: pointer;
  grid-gap: 0.5rem;
  gap: 0.5rem;
  color: var(--secondary-text);
  border-radius: 5px;
  text-transform: lowercase;
}

.channel::before {
  content: "#";
  font-size: 1.125rem;
  color: var(--secondary-text);
}

.channel:hover {
  background-color: var(--sidebar-hover);
  color: var(--primary-text);
} 

.channel-active {
  background-color: var(--main-bg-color);
  color: var(--primary-text)
}
/* Resets, Variables, Fonts */

:root {
    --primary-text: rgb(255, 255, 255);
    --secondary-text: rgb(114, 118, 125);
    --tag-text: rgb(185, 187, 190);
    --chat-text: rgb(114, 118, 125);
    --message-text: rgb(220, 221, 222);
    --primary-accent: rgb(88, 101, 242);
    --bg-color: rgb(32, 34, 37);
    --sidebar-bg-color: rgb(47, 49, 54);
    --sidebar-hover: rgb(49, 52, 58);
    --main-bg-color: rgb(54, 57, 63);
    --footer-bg-color: rgb(35, 39, 42);
    --chat-bg-color: rgba(255, 255, 255, 0.05);
    --green: rgb(67, 181, 129);
    --hyperlink: rgb(10, 164, 197);
    --code-bg-color: rgb(47, 49, 54);
    --code-border-color: rgb(32, 34, 37);
    --transition: all ease 250ms;
}

*,*::before,*::after{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color: rgb(32, 34, 37);
    background-color: var(--bg-color);
    color: #fff;
    height: calc(100vh - 1rem);
    font-family: sans-serif;
}

body::before {
    content: "CL Blog";
    position: absolute;
    font-size: 0.6rem;
    font-family: 'Seymour One', sans-serif;
    padding: 0.25rem 0.6rem;
    color: #72767D;
}

#root {
    display: flex;
    position: relative;
    top: 1rem;
}

a {
    text-decoration: none;
    color: inherit;
}
